import React from 'react';
import {
    BrowserRouter as Router,
    Link
  } from "react-router-dom";

import './adminslide.css'


   export default function AdminSlide (){
       const adminIcons = [
        {
            iconUrl: "../../static/img/bookkeeping.svg",
            title: "בתי ספר ",
            url:"/admin/school"

           }, 

        {
            iconUrl: "../../static/img/books.svg",
            title: "מורים",
            url:"/admin/teacher"

        },
        {
            iconUrl: '../../static/img/Claas.svg',
            title: "כיתות ",
            url:"/admin/class"

           },
           {
            iconUrl: "../../static/img/Subjcet.svg",
            title: "מקצועות ",
            url:"/profession"

           },

           {
            iconUrl: "../../static/img/student.svg",
            title: "תלמידים ",
            url:"/admin/student"

           },
           {
            iconUrl: "../../static/img/login.svg",
            title: "מערכת שעות ",
            url:"./schedule"

            
           },
           {
            iconUrl: "../../static/img/upload.svg",
            title: "ספרים וודיאו",
            url:"./studyMaterial"
            

           },
           {
            iconUrl: "../../static/img/exam.svg",
            title: "מבחנים ",
            url:"./exam"


           },
           {
            iconUrl: "../../static/img/result.svg",
            title: "ציונים ",
            url:"./result"

           },
           {
            iconUrl: "../../static/img/certificate.svg",
            title: "תעודות ",
            url:"./certificate"

           },
           {
            iconUrl: "../../static/img/event.svg",
            title: "אירועים ",
            url:"./event"

           },
           {
            iconUrl: "../../static/img/messages.svg",
            title: "הודעות ",
            url:"/admin/messages"

           },

           {
            iconUrl: "../../static/img/gallery.svg",
            title: "גלריה ",
            url:"/admin/gallery"

           },
       ]
    return(
        <div className="adminNav">
       {
        adminIcons.map(adminIcons => {
            return (

                
            <div className="adminFlex">
                 <div className="adminIcon" ><img src={adminIcons.iconUrl} alt={adminIcons.title} /></div>
                 <div className="adminLink"><Link to={adminIcons.url}>{adminIcons.title}</Link></div>
            </div>
            )
        })
        
       }
       
       </div>
)}

