import React from 'react';
import './klas.css';

export default class Klas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loaded: false,
      placeholder: "Teacher"
    };
  }

  async componentDidMount() {
    console.log("hello")
    const response = await fetch("/school/klas/")
    const jsonResponse = await response.json();
    console.log(jsonResponse)
    this.setState({data:jsonResponse['results']})
  }


  render() {
    return (
      <div>
        <div>כיתות</div>
        <ul>
          {this.state.data.map(Klas => <li key={Klas.id}>
            {Klas.name} {Klas.num}

          </li>)}        </ul>
      </div>
    );
  }
}
