import React from 'react';
import './teacher.css';

export default class Teacher extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loaded: false,
      placeholder: "Teacher"
    };
  }

  async componentDidMount() {
    console.log("hello")
    const response = await fetch("/school/teacher/")
    const jsonResponse = await response.json();
    console.log(jsonResponse)
    this.setState({data:jsonResponse['results']})
  }


  render() {
    return (
      <div>
        <div>מורים</div>
        <ul>
          {this.state.data.map(Teacher => <li key={Teacher.id}>
            {Teacher.frist_name} {Teacher.last_name}
                <img  src={"../../../../" + Teacher.image } />

          </li>)}        </ul>
      </div>
    );
  }
}
