import React from 'react';
import './gallery.css';

export default class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loaded: false,
      placeholder: "Teacher"
    };
  }

  async componentDidMount() {
    console.log("hello")
    const response = await fetch("/school/gallery/")
    const jsonResponse = await response.json();
    console.log(jsonResponse)
    this.setState({data:jsonResponse['results']})
  }


  render() {
    return (
      <div>
        <div>גלריה</div>
        <ul>
          {this.state.data.map(gallery => <li key={gallery.id}>
            {gallery.title} {gallery.image}

          </li>)}        </ul>
      </div>
    );
  }
}
