import React from 'react';
import './student.css'


export default class Student extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loaded: false,
      placeholder: "Student"
    };
  }

  async componentDidMount() {
    console.log("hello")
    const response = await fetch("/school/student/")


    const jsonResponse = await response.json();
    console.log(jsonResponse)
    this.setState({data:jsonResponse['results']})
  }

  render() {
    return (

      <div>
        <div>תלמידים</div>
        <div>

          {this.state.data.map(Student => <div key={Student.id}>
                <div>{Student.first} {Student.last}
                <img  src={"../../../../" + Student.image } />

                </div>

          </div>)}
        </div>
      </div>
    );
  }
}
