
import React from 'react';
import './Admin.css';
import { BrowserRouter as Router} from 'react-router-dom'
import AdminSlide from '../adminslide/AdminSlide'
import Route from "react-router-dom/Route";
import School from "../school/School";
import Teacher from "../teacher/Teacher";
import Student from "../student/Student";
import Search from "../search/Search";
import Klas from "../klas/Klas";
import Gallery from "../gallery/Gallery";

const Admin = () => (
    <div className="admin">
    <Router>
            <div className="mainAdmin">

        <Search/>
        <Route exact path="/admin/school"component={School}/>
        <Route exact path="/admin/teacher"component={Teacher}/>
        <Route exact path="/admin/student"component={Student}/>
        <Route exact path="/admin/class"component={Klas}/>
        <Route exact path="/admin/gallery"component={Gallery}/>



            </div>

    <AdminSlide/>

    </Router>

    </div>

);

export default Admin;




