import React from 'react';
import './userbar.css';


   export default function UserBar(){
       const icons = [
           {
            urlicon: "../../static/img/books.svg",
            title: "ספריה",
            url:"what"

           },
           
           {
            urlicon: "../../static/img/assimint.svg",
            title: "הגשת מטלות ",
            msg:"what"

           },
           {
            urlicon: "../../static/img/check.svg",
            title: "רישום נוכחות ",
            msg:"what"

           },
           {
            urlicon: "../../static/img/login.svg",
            title: "התחברות ",
            msg:"what"

           }
       ]
    return(
        <div className="iconClas">
       {
        icons.map(icon => {
            return (

                
            <div className="MainIcon">
                 <div className="MainIconImage"><img  src={process.env.PUBLIC_URL + icon.urlicon } /></div>

                 <div className="MainIconText"><a href={icon.url}>{icon.title}</a></div>
            </div>
            )
        })
       }
       </div>
)}