import React from 'react';
import './school.css';

export default class School extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loaded: false,
      placeholder: "School"
    };
  }

  async componentDidMount() {
    console.log("hello")

    const response = await fetch("/school/")
    const jsonResponse = await response.json();
    console.log(jsonResponse)
    this.setState({data:jsonResponse['results']})
  }

  render() {
    return (
      <div className="school">
        <div className="titleSchool">בתי ספר</div>
        <div className="mainSchool">
          {this.state.data.map(school => <div className="nameSchool" key={school.id}>
            {school.name}
            <img  src={process.env.PUBLIC_URL + school.image }/>
          </div>)}
        </div>
      </div>
    );
  }
}
